/* 项目容器 */
.projects-section {
    padding: 50px 0; /* 上下内边距 */
    text-align: center;
    background-color: #f9f9f9; /* 可选的背景颜色 */
    padding-bottom: 150px; /* 调整底部的间距，避免靠近页脚 */
  }
  
  .projects-title {
    font-size: 36px;
    font-weight: bold;
    margin-bottom: 50px;
  }
  
  /* 项目列表，使用flex布局 */
  .projects-list {
    display: flex;
    justify-content: center;
    gap: 40px;
    flex-wrap: wrap;
    margin-bottom: 50px; /* 额外的下方外边距 */
  }
  
  /* 每个项目的立方体 */
  .project-cube {
    width: 200px; /* 正方体的大小 */
    height: 200px;
    perspective: 1000px; /* 设置3D视角 */
  }
  
  .cube-inner {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.6s;
    transform-style: preserve-3d;
  }
  
  .project-cube:hover .cube-inner {
    transform: rotateY(180deg); /* 鼠标悬停时翻转 */
  }
  
  /* 正方体的前后面 */
  .cube-front,
  .cube-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden; /* 隐藏背面 */
    border-radius: 8px;
  }
  
  /* 正方体的正面样式 */
  .cube-front {
    background-color: #fff;
  }
  
  .cube-front img {
    width: 100%;
    height: 100%;
    border-radius: 8px;
    object-fit: cover; /* 保持图片比例 */
  }
  
  /* 正方体的背面样式 */
  .cube-back {
    background-color: #6C63FF;
    color: white;
    transform: rotateY(180deg); /* 背面需要预先旋转180度 */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px;
  }
  
  .cube-back h3 {
    margin-bottom: 10px;
  }
  
  .cube-back p {
    font-size: 14px;
  }
  