.team-page {
    padding: 50px 20px;
    text-align: center;
  }
  
  .team-list {
    display: flex;
    flex-direction: column; /* 竖向排列多个人物介绍 */
    gap: 30px; /* 每个人物介绍之间的间距 */
  }
  
  .team-member {
    display: flex; /* 每个人物介绍为水平排列，图片在左，文字在右 */
    align-items: center;
    background-color: #f9f9f9;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* 添加阴影效果 */
  }
  
  .team-member-image {
    width: 150px;
    height: 150px;
    border-radius: 50%; /* 圆形图片 */
    object-fit: cover;
    margin-right: 20px; /* 图片与文字之间的间距 */
  }
  
  .team-member-info {
    text-align: left; /* 左对齐文字 */
  }
  
  .team-member-info h3 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .team-member-info span {
    font-style: italic;
    font-weight: normal;
    color: #777;
  }
  
  .team-member-info p {
    font-size: 16px;
    margin-bottom: 8px;
  }
  